/* --- Global --- */
import {useState, useEffect} from 'react';
import {useForm} from 'react-hook-form';
import {withEthers, hooks} from '@ethers-react/system';

import {Overlay} from '@horizin/blueprint-system';

import FormClaim from './FormClaim';

/* --- Form : Component --- */
const DomainPurchase = props => {
  const ethersProvider = withEthers();
  const {handleSubmit, register, errors} = useForm();

  const transactionRequest = hooks.useWalletSendTransaction({});

  const [type, setType] = useState(undefined);
  const [isClaimOpen, setClaimIsOpen] = useState(false);
  const [isPurchaseOpen, setPurchaseIsOpen] = useState(false);
  const [subdomain, setSubDomain] = useState(undefined);

  const onSubmit = async values => {
    console.log(type, values, 'values');
    if (type === 'claim') setClaimIsOpen(true);
    if (type === 'purchase') setPurchaseIsOpen(true);

    setSubDomain(values.subdomain);
    // let tx = {
    //   to: values.address,
    //   value: ethersProvider.instance.utils.parseEther(values.amount),
    //   data: values.data,
    // };
    // transactionRequest.init(ethersProvider.wallet, tx);
  };

  useEffect(() => {
    console.log(transactionRequest);
  }, [transactionRequest]);

  return (
    <form
      onSubmit={handleSubmit(onSubmit)}
      style={{width: '100%', paddingLeft: 20, paddingRight: 20}}>
      <Atom.Flex alignCenter sx={{flexDirection: ['column', 'row']}}>
        <Atom.Flex alignCenter sx={{flex: [1, 1, 4], width: '100%'}}>
          <Molecule.Field
            name="subdomain"
            placeholder="Sub-Domain"
            register={register}
            errors={errors}
            sx={styles.field}
            sxWrapper={{
              flex: 3,
            }}
          />
          <Atom.Flex
            center
            sx={{
              bg: 'neutral',
              color: 'text',
              flex: 2,
              p: 3,
              height: 50,
              width: 200,
            }}>
            <Atom.Span sx={{}}>.milliondevs.eth</Atom.Span>
          </Atom.Flex>
        </Atom.Flex>

        <Atom.Flex sx={{flex: 1, width: '100%'}}>
          <FormClaim isOpen={isClaimOpen} subdomain={subdomain} />
        </Atom.Flex>
      </Atom.Flex>
    </form>
  );
};

const EarnOverlay = props => {
  return (
    // <Atom.Flex center column sx={{height: '100vh', width: '100vw'}}>
    <Atom.Box
      card
      sx={{
        position: 'absolute',
        top: 0,
        bottom: 0,
        left: 0,
        right: 0,
        margin: 'auto',
        width: 400,
        height: 400,
      }}>
      <Atom.Box card>hello world</Atom.Box>
    </Atom.Box>
    // </Atom.Flex>
  );
};

const styles = {
  field: {
    borderColor: 'gray',
    borderWidth: 1,
    borderStyle: 'solid',
    boxShadow: 0,
    flex: 3,
    height: 50,
    p: 10,
    // width: "100%"
  },
  button: {
    height: 50,
    flex: 1,
    width: 200,
  },
};

DomainPurchase.defaultProps = {
  label: 'Purchase',
};

export default DomainPurchase;
